button {
  border: 2px solid var(--color-link-transparent);
  background-color: #ffffff00;
  border-radius: 20px;
  color: var(--color-link);
  overflow: hidden;
  position: relative;
  text-decoration: none;
  transition: 0.2s transform ease-in-out;
  will-change: transform;
  z-index: 0;
  text-align: center;
  font-size: 1.vw !important;
  font-weight: 800;
  cursor: pointer;
  padding: 1vw 2vw !important;
}

button::after {
  background-color: var(--color-link-transparent);
  border-radius: 20px;
  content: '';
  display: block;
  height: 100%;
  width: 100%;
  position: absolute;
  left: 0;
  top: 0;
  transform: translate(-100%, 0) rotate(10deg);
  transform-origin: top left;
  transition: 0.2s transform ease-out;
  will-change: transform;
  z-index: -1;
}

button:hover::after {
  transform: translate(0, 0);
}

button:hover {
  border: 2px solid transparent;
  color: #4b0082;
  transform: scale(1.05);
  will-change: transform;
}

button a {
  font-size: 2rem;
}

.box {
  box-shadow: 5px 5px 5px 5px var(--color-box-shadow);
  border-radius: 10px;
  border: solid black 1px;
  margin: 3vw 10vw;
  padding: 2rem;
  transition: fade 0.4s ease-out;

}

/* Zoom in on img when hover. */
img {
  transition: transform .8s ease;
  overflow: hidden;
}

img:hover {
  transform: scale(1.06);
  transition: transform .8s ease;
}

.img-wrapper {
  padding: 1vw;
  border-radius: 20px;
  margin: 0rem;

  display: flex;
  flex-direction: column;
  align-items: center;
}

/* Loading screen */
.spinner {

  position: absolute;
  top: 48%;
  left: 48%;
  margin-right: -50%;
  transform: translate(-50%, -50%);
  display: inline-block;
  animation: spin 6s infinite linear;
}

.spinner svg {
  width: 60px;
  height: 80px;
  fill: black;
}

.loader-container {
  background-size: 100% 100%;
}

@keyframes fadeIn {
  from {
    opacity: 0;
  }

  to {
    opacity: 1;
  }
}

@-webkit-keyframes fadeinout {
  0%, 100% {
    opacity: 0;
  }

  50% {
    opacity: 1;
  }
}

@keyframes fadeinout {
  0%, 100% {
    opacity: 0;
  }

  50% {
    opacity: 1;
  }
}

@keyframes spin {
  0% {
    transform: rotateY(0deg);
  }

  50% {
    transform: rotateY(1800deg);
  }

  100% {
    transform: rotateY(3600deg);
  }
}

@keyframes spin-2 {
  0%, 100% {
    animation-timing-function: cubic-bezier(0.5, 0, 1, 0.5);
  }

  0% {
    transform: rotateY(0deg);
  }

  50% {
    transform: rotateY(1800deg);
    animation-timing-function: cubic-bezier(0, 0.5, 0.5, 1);
  }

  100% {
    transform: rotateY(3600deg);
  }
}






